import React from 'react';
import { TAppItem } from '@/routes/type';
import App from './app';

type IProps = {
  item: TAppItem;
  onClick?: any;
};

const Application = ({
  item,
  onClick
}: IProps) => {
  const gotoUrl = (url: string) => { window.open(url); };
  return (
    <App item={item} onClick={() => (onClick && onClick(item)) || (item.url && gotoUrl(item.url))} />
  );
};

export default Application;
